import { Component, useEffect, ChangeEvent } from "react";
import { ethers } from "ethers";
import contract from "../contracts/project-dragon-token-beta.json";

declare let window:any;

class ContractMetaMaskService {
    async connect() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }
        
        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        let userAddress = await signer.getAddress();
        let network = await provider.getNetwork();

        if(network.chainId !== 5) {
            let account = JSON.stringify({ "connected": true, "userAddress": userAddress, "chainId": network.chainId, "chainName": network.name });
        
            return account;
        }

        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let balanceOf = await contractinfo.balanceOf(userAddress);

        let account = JSON.stringify({ "connected": true, "userAddress": userAddress, "chainId": network.chainId, "chainName": network.name, "balanceOf": ethers.utils.formatEther(balanceOf) });
        
        return account;
    }

    async getBalanceOf() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();
        
        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);
        let userAddress = await signer.getAddress();
        let balanceOf = await contractinfo.balanceOf(userAddress);

        let info = JSON.stringify({ "balanceOf": ethers.utils.formatEther(balanceOf) });

        return info;
    }

    async claimToken() {
        if(!window.ethereum) {
            let installmetamask = JSON.stringify({ "installmetamask": false });
            return installmetamask;
        }

        const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        const contractinfo = new ethers.Contract(contract.address, contract.abi, signer);

        try {
            const tx = await contractinfo.claimToken();
            let info = JSON.stringify({ "tx": tx });

            return info;
        } catch (e:any) {
            let error = JSON.stringify({ "error": e });
            return error;
        }
        
    }

}

export default new ContractMetaMaskService();