import React, { useState, useEffect, ChangeEvent } from "react";
import ContractMetaMaskService from "./services/contract-metamask.service";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import ScrollToTop from "./ScrollToTop";

import { Button, Modal } from 'react-bootstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import { Bars } from  'react-loader-spinner'
import CoinBg2 from "./CoinBg2";

declare let window: any;

const App = () => {

  const [connectWalletModalShow, setConnectWalletModalShow] = React.useState(false);
  const [alertModalShow, setAlertModalShow] = React.useState(false);
  const [alertMessage, setAlertMessage] = useState<string | undefined>();

  const [currentAccount, setCurrentAccount] = useState<string | undefined>();
  const [chainId, setChainId] = useState<number | undefined>();
  const [chainName, setChainName] = useState<string | undefined>();
  const [balanceOf, setBalanceOf] = useState<string | undefined>();

  //#region Toastify
  const toastNormal = (header: any) => (
    <div className="toast-div">
      <h5 className="toast-header">{header}</h5>
    </div>
  );

  const toastError = (header: any) => (
    <div className="toast-div">
      <h5 className="toast-header">{header}</h5>
    </div>
  );

  const toastTxHash = (header: any, txHash: any) => (
    <div className="toast-div">
      <h5>{header}</h5>
      <a href={txHash} target="_blank" rel="noreferrer">view transaction</a>
    </div>
  );

  function showToast(header: any) {
    toast.info(toastNormal(header), {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
      progress: undefined,
    });
  }

  function showToastError(header: any) {
    toast.error(toastNormal(header), {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
      progress: undefined,
    });
  }

  function showToastTxLink(hash: any) {
    toast.success(toastTxHash("tx Hash Received", "https://goerli.etherscan.io/tx/" + hash), {
      position: "bottom-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
      progress: undefined,
    });
  }
  //#endregion

  //#region Handle Metamask Change
  function handleAccountsChanged(accounts: any) {
    if (accounts.length === 0) {
      setCurrentAccount(undefined);
      setChainId(undefined);
      setChainName(undefined);
      setConnectWalletModalShow(false);
      setAlertModalShow(false);
      setAlertMessage(undefined);
    } else if (accounts[0] !== currentAccount) {
      connectMetaMask();
    }
  }

  function handleChainChanged() {
    window.location.reload();
  }
  //#endregion

  async function connectMetaMask() {
    let response = await ContractMetaMaskService.connect();
    let data = JSON.parse(response);
    if(data.installmetamask === false) {
      setAlertMessage("Please install MetaMask!");
      setAlertModalShow(true);
      setConnectWalletModalShow(false);
    }
    else {
      setCurrentAccount(data.userAddress);
      setChainId(data.chainId);
      setChainName(data.chainName);
      setBalanceOf(data.balanceOf);
      setConnectWalletModalShow(false);
    }
  }

  async function disconnectMetaMask() {
    setCurrentAccount(undefined);
    setChainId(undefined);
    setChainName(undefined);
    setBalanceOf(undefined); 
  }

  async function claim() {
    if(!currentAccount) {
      alert("Please connect account!");
    }
    else if (chainId !== 5) {
      setAlertMessage("You connected a wrong network!");
      setAlertModalShow(true);
    }
    else {
      let response = await ContractMetaMaskService.claimToken();
      showToast("Request Sent");
      let data = JSON.parse(response);
      if(data.hasOwnProperty("error")){
        showToastError("Error");
        if(data.error.message) {
          setAlertMessage(data.error.message); // User denied transaction signature
        }
        else if(data.error.error.message) {
          setAlertMessage(data.error.error.message); // Contract require limitation
        }
        setAlertModalShow(true);
      }
      else if(data.installmetamask === false) {
        showToastError("Error");
        setAlertMessage("Please install MetaMask!");
        setAlertModalShow(true);
      }
      else if(data.tx.hash){
        showToastTxLink(data.tx.hash);
        let response = await ContractMetaMaskService.getBalanceOf();
        let data_balanceOf = JSON.parse(response);
        if(data.installmetamask === false) {
          setAlertMessage("Please install MetaMask!");
          setAlertModalShow(true);
          setConnectWalletModalShow(false);
        }
        else {
          setBalanceOf(data_balanceOf.balanceOf);
        }

      }
      else {
        //alert(data.message);
      }
    }
  }

  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', handleAccountsChanged);
      window.ethereum.on("chainChanged", handleChainChanged);
    }
  }, []);

  return (
    <div>
      <div className="front">
        <div className="content">
          <CoinBg2 />

          <div className="content-text">
          <h1>Dragon Token Beta (DGT)</h1>
          <p className="info">An ERC-21 Token Beta Project on Goerli Testnet. Claim your 10 Airdrop DGT.</p>
          {currentAccount
            ? <>
              <button className="btn btn-info" onClick={claim}>
                Claim
              </button>
              <button className="btn btn-outline-info ms-2" onClick={disconnectMetaMask}>
                Disconnect
              </button>
              </>
            : <button className="btn btn-info" onClick={() => setConnectWalletModalShow(true)}>
                Connect Wallet
              </button>
          }

          <a href="https://github.com/ksin8909/Project-Number-NFT-Beta/blob/main/smart-contract/project-number-nft-beta.sol" target="_blank" rel="noreferrer" className="btn btn-outline-secondary ms-2">
            GitHub
          </a>
          
          {currentAccount
            ? <>
                <p className="mt-3 connect-info">Address: {currentAccount}</p>
                <p className="connect-info">Your DGT Balance: {balanceOf}</p>
              </>
            : ""
          }
          </div>

          <Modal show={connectWalletModalShow} onHide={() => setConnectWalletModalShow(false)} size="lg" aria-labelledby="connect-wallet-modal" centered>
            <Modal.Header className="text-center" closeButton>
              <Modal.Title className="w-100" id="connect-wallet-modal">
                Connect Wallet
              </Modal.Title>
            </Modal.Header>
            <Modal.Body className="connect-wallet-modal-body">
              <button className="btn btn-outline-secondary w-50" onClick={connectMetaMask}>
                <img src={process.env.PUBLIC_URL + "/images/MetaMask_Fox.svg.png"} alt="" width="60px" height="60px" /> Metamask
              </button>
            </Modal.Body>
            <Modal.Footer>
              <button className="btn btn-danger" onClick={() => setConnectWalletModalShow(false)}>
                Close
              </button>
            </Modal.Footer>
          </Modal>

          <Modal show={alertModalShow} onHide={() => setAlertModalShow(false)} size="lg" aria-labelledby="alert-modal" centered>
            <Modal.Header className="text-center" closeButton>
              <Modal.Title className="w-100" id="alert-modal">
                Alert
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <h4 className="mb-0">{alertMessage}</h4>
            </Modal.Body>
            <Modal.Footer>
              <button className="btn btn-danger" onClick={() => setAlertModalShow(false)}>
                Close
              </button>
            </Modal.Footer>
          </Modal>

        </div>
        
      </div>
      <ToastContainer />

    </div>
  )
}

export default App